import React, { useState } from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Box from '@mui/material/Box';
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import ScrollToTop from './components/ScrollToTop';
import ScrollToTopButton from './components/ScrollToTopButton';
import Home from './pages/Home';
import Instructions from './pages/Instructions';
import Materials from './pages/Materials';
import Catalog from './pages/Catalog';
import Tips from './pages/Tips';
import PageTransition from './components/PageTransition';
import { AnimatePresence } from 'framer-motion';

function App() {
  const [darkMode, setDarkMode] = useState(true);

  const theme = createTheme({
    palette: {
      mode: darkMode ? 'dark' : 'light',
      primary: {
        main: darkMode ? '#DEB887' : '#8B4513',
        light: darkMode ? '#E6D5C3' : '#A0522D',
        dark: darkMode ? '#B8860B' : '#654321',
      },
      background: {
        default: darkMode ? '#121212' : '#f5f5f5',
        paper: darkMode ? '#1a1a1a' : '#ffffff',
      },
      text: {
        primary: darkMode ? '#FFFFFF' : '#121212',
        secondary: darkMode ? 'rgba(255, 255, 255, 0.7)' : 'rgba(0, 0, 0, 0.7)',
      },
    },
    typography: {
      fontFamily: '"Jura", "Helvetica", "Arial", sans-serif',
      h1: {
        fontWeight: 700,
      },
      h2: {
        fontWeight: 700,
      },
      h3: {
        fontWeight: 700,
      },
      h4: {
        fontWeight: 700,
      },
      h5: {
        fontWeight: 700,
      },
      h6: {
        fontWeight: 700,
      },
    },
    components: {
      MuiButton: {
        styleOverrides: {
          root: {
            textTransform: 'none',
            borderRadius: 8,
            fontWeight: 600,
            padding: '8px 24px',
            fontSize: '1.1rem',
          },
        },
      },
      MuiPaper: {
        styleOverrides: {
          root: {
            borderRadius: 12,
          },
        },
      },
    },
  });

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <ScrollToTop />
        <Box sx={{ 
          display: 'flex', 
          flexDirection: 'column',
          minHeight: '100vh'
        }}>
          <Navbar isDarkMode={darkMode} toggleTheme={() => setDarkMode(!darkMode)} />
          <Box component="main" sx={{ 
            flexGrow: 1
          }}>
            <AnimatePresence mode="wait">
              <Routes>
                <Route path="/" element={<PageTransition><Home isDarkMode={darkMode} /></PageTransition>} />
                <Route path="/catalog" element={<PageTransition><Catalog isDarkMode={darkMode} /></PageTransition>} />
                <Route path="/instructions" element={<PageTransition><Instructions isDarkMode={darkMode} /></PageTransition>} />
                <Route path="/materials" element={<PageTransition><Materials isDarkMode={darkMode} /></PageTransition>} />
                <Route path="/tips" element={<PageTransition><Tips isDarkMode={darkMode} /></PageTransition>} />
                <Route path="*" element={<PageTransition><Home isDarkMode={darkMode} /></PageTransition>} />
              </Routes>
            </AnimatePresence>
          </Box>
          <Footer />
          <ScrollToTopButton />
        </Box>
      </Router>
    </ThemeProvider>
  );
}

export default App;
